.colors,
.furniture{
  margin-top: 1.875em;
  display: flex;
}

.furniture-group,
.colors-group{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 1.25em;
  &:last-child{
    margin: 0;
  }
  &__items{
    padding: .25em;
    border: 1px solid $gray;
    border-radius: 3.875em;
    margin-top: .625em;
    display: flex;
  }
}

.furniture-field,
.color-field{
  label{
    transition: all .3s;
    width: 2.8125em;
    height: 2.8125em;
    border-radius: 50%;
    padding: .1875em;
    display: block;
    border: .125em solid transparent;
    img{
      width: 100%;
      height: 100%;
      display: block;
      border-radius: inherit;
      object-fit: cover;
      position: relative;
    }
    &:after{
      content: '';
      @include centerAbsolute;
      width: calc(100% - .625em);
      height: calc(100% - .625em);
      border-radius: inherit;
      border: 1px solid rgba($white,.2);
    }
  }
  input:checked{
    &~label{
      border-color: $primary;
    }
  }
  input:hover{
    &~label{
      border-color: $white4;
    }
  }
}

.furniture-field{
  label{
    width: 3.4375em;
    height: 3.4375em;
  }
  input:hover{
    &~label{
      background-color: $white;
      border-color: $white;
      transform: scale(1.45);
    }
  }
}

.step-props2{
  margin-top: 1.875em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  .step-prop{
    margin-bottom: 1.875em;
    &:last-child{
      margin: 0;
    }
  }
}

.props{
  margin-top: 1.875em;
  display: flex;
  align-items: flex-start;
}

.prop-item{
  min-width: 9.375em;
  margin-right: .625em;
  @include grid100gap(.3125em);
  color: $gray3;
  &:last-child{
    margin: 0;
  }
  &__title{
    font-size: 1.0625em;
    line-height: 150%;
  }
  &__desc{
    font-size: 2.125em;
    line-height: 120%;
    font-family: $font2;
    span{
      font-family: $font3;
      font-size: .62em;
    }
  }
}

.step-nav{
  margin-top: 1.875em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .5em));
  gap: .5em 1em;
  width: 50em;
  max-width: 100%;
}

.step-equip{
  margin-top: 2.5em;
  @include grid100gap(1.25em);
}


@media screen and (max-width: 1400px){
  .furniture,
  .colors{
    margin-top: 1.25em;
  }

  .furniture-group,
  .colors-group{
    &__title{
      font-size: .8125em;
    }
  }

  .color-field{
    @media screen and (min-width: 993px){
      label{
        width: 2.5em;
        height: 2.5em;
      }
    }
  }

  .furniture-field{
    label{
      width: 3.125em;
      height: 3.125em;
    }
  }

  .step-props2{
    margin-top: 1.25em;
    flex-direction: row;
    .step-prop{
      flex-shrink: 0;
      margin: 0 1.875em 0 0;
    }
  }

  .props{
    margin-top: 1.25em;
  }

  .prop-item{
    font-size: .875em;
  }

  .step-equip{
    margin-top: 1.5em;
    gap: 1em;
    &__desc{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 992px){
  .furniture{
    margin-top: .9375em;
    display: block;
  }

  .colors{
    margin-top: 1.875em;
    display: block;
  }

  .furniture-group,
  .colors-group{
    margin: 0 0 .9375em;
  }

  .step-props2{
    margin-top: .9375em;
    flex-direction: column;
    .step-prop{
      margin: 0 0 .9375em;
    }
  }

  .prop-item{
    font-size: .75em;
    min-width: 8.75em;
  }

  .step-nav{
    grid-template-columns: 100%;
  }
}


@media screen and (max-width: 576px){
  .furniture-group,
  .colors-group{
    &__title{
      font-size: .6875em;
    }
    &__items{
      margin-top: .3125em;
    }
  }
}
