.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 37.5em;
        max-width: calc(100% - 1em);
        position: relative;
        z-index: 3;
        padding: 3.75em 6.25em;
        display: inline-block;
    }
    .close-btn{
        position: absolute;
        top: 1.875em;
        right: 1.875em;
    }
    &__hint{
        margin-top: 1.875em;
        text-align: center;
        color: $gray;
        a{
            color: $primary;
        }
    }
}

.close-btn{
    width: 2.25em;
    @include center;
    flex-shrink: 0;
    svg{
        width: 100%;
    }
    &:hover{
        path{
            stroke: $black;
        }
    }
}

.modal-form{
    margin-top: 1.875em;
    @include grid100gap(.9375em);
}

.thank-modal{
    &__inner{
        text-align: center;
        padding: 4.25em 0;
        @include grid100gap(1.875em);
    }
    .m-btn{
        padding: 0;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {

        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {
            padding: 3em 1em 2em;
        }
        .close-btn{
            top: .5em;
            right: .5em;
        }
        .h2{
            font-size: 1.5em;
        }
        &__hint{
            font-size: .875em;
        }
    }

    .modal-form{
        margin-top: 1em;
        gap: .5em;
    }

    .thank-modal{
        &__inner{
            padding: 2em 0;
        }
    }
}
