._flex{
  display: flex;
  align-items: center;
}

.step{
  min-height: $vh;
  background-color: $white2;
  padding: 2.5em 4.25em 5em 6.25em;
  display: grid;
  grid-template-columns: 50em calc(100% - 53.75em);
  justify-content: space-between;
  &__col{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__col2{
    display: flex;
    align-items: center;
    &._center{
      justify-content: center;
    }
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.875em;
    display: flex;
  }
}

.step-top{
  margin-bottom: 3.125em;
  &__link{
    display: flex;
    align-items: center;
    &:before{
      content: '';
      flex-shrink: 0;
      width: 1px;
      height: 1.25em;
      margin: 0 1.25em;
      background-color: $gray2;
    }
    span{
      font-size: 1.0625em;
      line-height: 150%;
    }
  }
}

.step-progress{
  display: flex;
  align-items: center;
  &__item{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    min-width: 5.625em;
    position: relative;
    color: $gray3;
    &:last-child{
      min-width: unset;
      flex-grow: unset;
      &:after{
        display: none;
      }
    }
    &:after{
      content: '';
      position: absolute;
      top: .78125em;
      left: 0;
      transform: translateY(-50%);
      width: 100%;
      height: 2px;
      background-color: $white4;
    }
    &:before{
      content: '';
      width: 1.5625em;
      height: 1.5625em;
      border-radius: 50%;
      background-color: $white4;
      background-image: url(../img/check.svg);
      @include bgImgSize(0);
      margin-bottom: .625em;
      position: relative;
      z-index: 2;
    }
    span{
      font-size: 1.0625em;
      line-height: 150%;
    }
    &._active{
      &:before{
        background-color: $gray2;
      }
    }
    &._done{
      &:before{
        background-color: $primary;
        background-size: .9375em;
      }
    }
  }
}

.step-text{
  margin-top: .9375em;
  @include grid100gap(.9375em);
  &__desc{
    font-size: 1.0625em;
    line-height: 150%;
  }
}


@media screen and (max-width: 1400px){
  .step{
    padding: 1.875em 1.625em 1.875em 3.75em;
    grid-template-columns: 33em calc(100% - 34.375em);
    &__nav{
      margin-top: 0;
    }
  }

  .step-top{
    &__title{
      font-size: 1.4375em;
    }
    &__link{
      span{
        font-size: .8125em;
      }
    }
  }

  .step-progress{
    &__item{
      &:before{
        margin-bottom: .3125em;
      }
      span{
        font-size: .8125em;
      }
    }
  }

  .step-text{
    max-width: 20em;
    .h1{
      font-size: 2.0625em;
    }
    &__desc{
      font-size: .8125em;
    }
  }
}


@media screen and (max-width: 992px){
  .step{
    position: relative;
    padding: 8.125em 3.125em 3.75em;
    grid-template-columns: 21.125em calc(100% - 23em);
    &__col2{
      flex-direction: column;
    }
  }

  .step-top{
    position: absolute;
    top: 1.875em;
    left: 0;
    width: 100%;
    flex-direction: column;
    margin: 0;
    &__title{
      font-size: 1.625em;
    }
    &__link{
      order: -1;
      flex-direction: column;
      &:before{
        order: 2;
        width: 1.25em;
        height: 1px;
        margin: .625em 0;
      }
    }
  }

  .step-progress{
    width: 100%;
    &__item{
      min-width: 6.25em;
      &:before{
        margin-bottom: .625em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .step{
    padding: 7em 1.25em 1.25em;
    grid-template-columns: 100%;
    gap: 1.875em;
    &__col2{
      order: -1;
      flex-direction: row;
      justify-content: space-between;
    }
  }

  .step-top{
    top: 1.25em;
    &__title{
      font-size: 1.5em;
    }
    &__link{
      span{
        font-size: .6875em;
      }
    }
  }

  .step-progress{
    &__item{
      flex-grow: 1;
      min-width: unset;
    }
  }

  .step-text{
    &__desc{
      font-size: .6875em;
    }
  }
}
