.fg {
  position: relative;
  margin: 0;
  input{
    border-radius: 0;
    border: 1px solid $gray;
    padding: 0 1.5em;
    height: 4.11em;
    width: 100%;
    font-size: 1.0625em;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: $gray;
    }
    &:focus {
      border-color: $primary;
    }
  }
  &__field{
    position: relative;
  }
}

.m-input{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}


@media screen and (max-width: 1400px){
  .fg{
    input{
      height: 3.53em;
    }
  }
}


@media screen and (max-width: 992px){
  .fg{
    input{
      height: 3.3em;
    }
  }
}


