.step-field{
  padding: 2.5em 6.75em 0 3.625em;
  display: flex;
  align-items: center;
  &__img{
    width: 16.75em;
    @include center;
    position: relative;
    img{
      max-width: 100%;
      max-height: 100%;
    }
    &>img{
      position: relative;
      z-index: 2;
    }
  }
  &__remove{
    position: absolute;
    bottom: 13.25em;
    left: 100%;
    margin-left: .625em;
    width: 2.5em;
    height: 2.5em;
    background-color: $white;
    @include center;
    border-radius: 50%;
    svg{
      width: .875em;
    }
    &--cell2{
      bottom: 27.25em;
    }
  }
}

.step-sizes{
  align-self: flex-start;
  padding: 5.25em 0;
  margin-right: 2em;
  position: relative;
  z-index: 3;
  width: 5.3125em;
}

.size-item{
  margin-bottom: 2.75em;
  position: relative;
  &:last-child{
    margin-bottom: 0;
  }
  &._disabled{
    pointer-events: none;
    .size-item__btn{
      background-color: $white3;
      color: $gray;
    }
  }
  &._show{
    .size-item__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__btn{
    background-color: $gray2;
    height: 2.5625em;
    width: 100%;
    @include center;
    border-radius: 1.3125em;
    color: $white;
    span{
      font-size: .9375em;
    }
    &:hover{
      color: $white;
      background-color: $primary;
    }
  }
  &__hidden{
    position: absolute;
    left: 100%;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    padding-left: .625em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__variants{
    display: flex;
  }
  &__variant,
  &__close{
    @include center;
    box-shadow: 0 .125em 1em rgba(36, 32, 32, 0.15);
    background-color: $white2;
    border-radius: 50%;
    border: 1px solid $white3;
  }
  &__variant{
    width: 5em;
    height: 5em;
    margin-right: .625em;
    img{
      width: 2.25em;
    }
  }
  &__close{
    width: 2.5em;
    height: 2.5em;
    img{
      width: .875em;
    }
  }
}

.step-props{
  flex-grow: 1;
  @include grid100gap(1.875em);
}

.step-prop{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__items{
    border-radius: 3.875em;
    margin-top: .625em;
    padding: .25em;
    display: flex;
    background-color: $gray3;
  }
  &__item{
    flex-grow: 1;
    label{
      border-radius: 3.875em;
      padding: 0 .9375em;
      width: 100%;
      height: 2.875em;
      @include center;
      color: $white2;
      transition: all .3s;
    }
    span{
      font-size: 1.0625em;
      line-height: 150%;
    }
    ._view2{
      display: none;
    }
    input:checked{
      &~label{
        background-color: $primary;
      }
    }
  }
}

.step-back{
  display: flex;
  align-items: center;
  margin-bottom: 2.5em;
  color: $gray2;
  &:hover{
    color: $primary;
    rect,
    path{
      stroke: $primary;
    }
  }
  svg{
    width: 1.75em;
    margin-right: .625em;
  }
  span{
    font-size: 1.0625em;
  }
}


@media screen and (max-width: 1400px){
  .step-field{
    padding: 0 4.375em 0 0;
    &__img{
      width: 11.375em;
    }
    &__remove{
      bottom: 9em;
      &--cell2{
        bottom: 18em;
      }
    }
  }

  .step-sizes{
    padding: 0;
  }

  .size-item{
    margin-bottom: 2em;
  }

  .step-props{
    gap: 1.5625em;
  }

  .step-prop{
    &__title{
      font-size: .8125em;
    }
    &__items{
      margin-top: .3125em;
      padding: .1875em;
    }
    &__item{
      label{
        padding: 0 .5em;
        height: 1.875em;
      }
      span{
        font-size: .8125em;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .step-back{
    margin-bottom: 1.25em;
    span{
      font-size: .8125em;
    }
  }
}


@media screen and (max-width: 992px){
  .step-field{
    padding: 0;
    &__img{
      width: 9.25em;
    }
    &__remove{
      bottom: 7em;
      width: 2em;
      height: 2em;
      svg{
        width: .75em;
      }
      &--cell2{
        bottom: 14em;
      }
    }
  }

  .step-sizes{
    font-size: .875em;
  }

  .step-props{
    display: block;
    margin-top: 1.875em;
  }

  .step-prop{
    margin-bottom: .9375em;
    &:last-child{
      margin-bottom: 0;
    }
  }

  .step-back{
    margin-bottom: 1.875em;
  }
}


@media screen and (max-width: 576px){
  .step-sizes{
    font-size: .625em;
    margin-right: 1em;
  }

  .step-field{
    &__img{
      width: 5em;
    }
    &__remove{
      bottom: 6em;
      font-size: .625em;
      margin-left: .25em;
      &--cell2{
        bottom: 13em;
      }
    }
  }

  .size-item{
    margin-bottom: 1em;
  }

  .step-props{
    margin: 0 0 0 .625em;
    flex-grow: unset;
  }

  .step-prop{
    &__title{
      font-size: .6875em;
    }
    &__item{
      label{
        padding: 0 .3125em;
        height: 1.6875em;
      }
      span{
        font-size: .6875em;
      }
    }
  }

  .step-back{
    span{
      font-size: .6875em;
    }
  }
}
