.main{
  display: grid;
  grid-template-columns: repeat(2,50%);
  grid-template-rows: repeat(2,50%);
  min-height: $vh;
  &__img{
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
  &__info{
    background-color: $white2;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    padding: 15.25em 5em;
    @media screen and (min-width: 993px){
      grid-row: 1/3;
      grid-column: 2/3;
    }
  }
  &__info2{
    padding: 5.625em 5em;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  &__subtitle{
    font-size: 2.5em;
    line-height: 110%;
    text-transform: uppercase;
    color: $gray;
  }
  .h0{
    color: $gray2;
  }
  &__text{
    @include grid100gap(.9375em);
    color: $gray2;
  }
  &__btn{
    margin-top: 2.5em;
  }
}


@media screen and (max-width: 1400px){
  .main{
    &__info{
      padding: 7.5em 3.75em;
    }
    &__info2{
      padding: 3.75em;
    }
    &__subtitle{
      font-size: 1.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    &__img{
      grid-column: 1/-1;
    }
    &__info{
      padding: 3.5em 2.5em;
      justify-content: unset;
    }
    &__info2{
      padding: 2.5em 3.125em;
    }
    &__subtitle{
      font-size: 1.375em;
    }
    &__btn{
      margin-top: 1.625em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    display: flex;
    flex-direction: column;
    &__img{
      @include imgCover(55.5%);
    }
    &__info2{
      padding: 4em 1.25em;
    }
    &__info{
      padding: 3.125em 1.25em;
      flex-grow: 1;
    }
    &__subtitle{
      font-size: 1.25em;
      margin-bottom: .25em;
    }
  }
}
