@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 5.125em;
    height: 4.375em;
    font-family: $font2;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.625em;
    }
    img{
        width: 2.1875em;
        margin-left: .9375em;
    }
}

.m-btn-green{
    color: $white;
    background-color: $primary;
    &:hover{
        color: $white;
        background-color: darken($primary,10%);
    }
    &:disabled{
        pointer-events: none;
        color: $white;
        background-color: $gray;
    }
}


.m-btn-black-outline{
    border: 1px solid $gray2;
    color: $gray2;
    &:hover{
        color: $white;
        background-color: $gray2;
    }
}



@media screen and (max-width: 1400px){
    .m-btn{
        height: 3.75em;
        padding: 0 2.5em;
        span{
            font-size: 1.5em;
        }
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 3.5em;
        span{
            font-size: 1.375em;
        }
    }
}


